import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { PatientHouseholdMonitoringProvider } from '../../providers/patient-household-monitoring/patient-household-monitoring';
import { PatientMonitoringMapper } from '../../models/patient-monitoring.model';

/**
 * Generated class for the PatientMonitoringHistoryPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-patient-monitoring-history',
  templateUrl: 'patient-monitoring-history.html',
})
export class PatientMonitoringHistoryPage {

  currentItems: any[];

  type: string = 'household';

  constructor(public navCtrl: NavController, public navParams: NavParams,
              private patientHouseholdMonitoring: PatientHouseholdMonitoringProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad PatientMonitoringHistoryPage');
    this.getItems();
  }

  getItems(params?: any) {
    return new Promise((resolve) => {
      this.patientHouseholdMonitoring.query(params).subscribe((data: PatientMonitoringMapper[]) => {
        if (params && params.q) {
          this.currentItems = data;
          resolve();
          return;
        }
        this.currentItems = this.currentItems.concat(data);
        resolve();
      });
    });
  }

  filterByMonitoringType(type) {

  }

}



